.Footer {
    width: 100%;
    font-size: 1rem;
}

.Footer__wrapper {
    padding-top: 1em; 
    padding-bottom: 2em;
    border-top: 1px solid #E5E5E5;
}

.Footer__paragraph {
    font-size: 1.2em;
    text-align: center;
    color: #A6A6A6;
}

@media only screen and (min-width: 600px) {
    .Footer__paragraph {
        font-size: 1.3em;
    }
}


