.Typewriter__cursor {
    -webkit-animation: cursor 1700ms infinite 1500ms;
            animation: cursor 1700ms infinite 1500ms;
    background-color: #A6A6A6;
}

@-webkit-keyframes cursor {
    50% {
        background-color: transparent;
    }
}

@keyframes cursor {
    50% {
        background-color: transparent;
    }
}


