html {
  font-size: 62.5%;
  height: 100%;
}

body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #FFFAFA;
}

.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 0;
}

.icon {
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
  margin: 0 auto;
}

@media only screen and (min-width: 600px) {
  .container {
    padding: 0 3rem;
  }
}


