.Projects {
    width: 100%;
    font-size: 1.6rem;
}

.Projects__wrapper {
    padding-top: 0;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-name: fadein;
            animation-name: fadein;
    max-width: 600px;
}

@-webkit-keyframes fadein {
    from {
        opacity: 0;
        padding-top: 3.5em;
    }

    to {
        opacity: 1;
        padding-top: 0;
    }
}

@keyframes fadein {
    from {
        opacity: 0;
        padding-top: 3.5em;
    }

    to {
        opacity: 1;
        padding-top: 0;
    }
}

.Project {
    display: -ms-flexbox;
    display: flex;
    color: #666666;
    margin-bottom: 0;
    width: 100%;
    height: 100%;
    border: 1px solid #EBEBEB; 
    border-top: none;
    background-color: white;
}

.Project__left {
    padding: 1em 0.5em 1em 1em;
}

.Project__right {
    font-size: 1em;
    line-height: 1.5em;
    padding: 1em;
}

.Project__image {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover; 
    width: 4em;
    height: 8em;
}

.Project__title {
    color: black;
    margin-top: 0;
    font-weight: normal;
    font-size: 1em;
}

.Project__link {
    text-decoration: none;
}

.Project__description {
    font-size: 0.9em;
}

.Project__tags {
    font-size: 0.8em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.Project__tag {
    border-bottom: 1px solid #F80000;
    margin: 0 0.5em 0.7em 0;
    padding: 0.1em 0.2em;

}

@media only screen and (min-width: 600px) {
    .Project__title {
        font-size: 1.17em;
    }

    .Project__description {
        font-size: 0.9em;
    }

    .Project__tags {
        font-size: 0.8em;
    }

    .Project__image {
        width: 6em;
        height: 100%;
    }
}



