.Intro {
    width: 100%;
    font-size: 1rem;
}

.Intro__wrapper {
    padding: 15em 1em 3em 1em;
}

.Intro__wrapper--card {
    border-bottom: 1px solid #E5E5E5;
    text-align: center;
}

.Intro__title {
    font-size: 3em;
    margin: 0.5em auto 0.1em;
}

.Intro__subtitle {
    font-size: 1.7em;
    line-height: 1.2em;
    margin: 0.5em auto 1em auto;
    color: #A6A6A6;
}

@media only screen and (min-width: 600px) {
    .Intro__wrapper {
        padding: 17em 0 3em 0;
    }

    .Intro__title {
        font-size: 4em;
    }

    .Intro__subtitle {
        font-size: 1.8em;
        line-height: 1.5em;
    }
}


